import { TImage } from "@/types";


export interface IGalleryProps {
    id: number;
    documentId: string;
    __component: string;
    heading: string;
    subHeading: string;
    description: string;
    images: TImage[];
}

export function Gallery({ data }: { data: IGalleryProps }) {
    if (!data) return null;

    const { heading, subHeading, description, images } = data;
    return (
        <section className="px-8 pb-6 mx-auto md:px-6 lg:py-2 bg-brand-cream">
            <div className="container mx-auto max-w-6xl">
                <div className="mb-12 text-center">
                    <h2 className="mb-2 text-xl text-brand-gold font-extrabold">{heading}</h2>
                    <p className="text-xl text-black">{subHeading}</p>
                    {description && (
                        <p className="mx-auto mt-4 max-w-2xl text-brand-dark-grey">{description}</p>
                    )}
                </div>
               <div className="rounded-xl border bg-white my-4 py-6 px-10 shadow-sm transition-shadow hover:shadow-md">
                    
                </div>
            </div>
        </section>
    )
}